<template>
  <input ref="peerLink" :value="peerLink" readonly>
  <button @click="copyPeerLink">Copy</button>
  <video ref="mainView" class="main-view"></video>
  <video ref="selfView" class="self-view"></video>
</template>

<script>
import Peer from "peerjs";

export default {
  name: "App",
  data() {
    return { peer: null, peerId: null };
  },
  
  computed: {
    peerLink() {
      if (!this.peerId) return "";
      return document.location.origin + document.location.pathname + "?p=" + this.peerId;
    }
  },

  methods: {
    copyPeerLink() {
      this.$refs.peerLink.focus();
      this.$refs.peerLink.select();
      document.execCommand("copy");
    }
  },

  created() {
    this.peer = new Peer();
    this.peer.on("open", id => (this.peerId = id));

    navigator.mediaDevices.getUserMedia({ audio: true, video: true })
    .then(function(mediaStream) {
      this.$refs.selfView.srcObject = mediaStream;
      this.$refs.selfView.onloadedmetadata = () => this.$refs.selfView.play();
    });
  }
};
</script>

<style>
input {
  font-size: 16px;
  padding: 8px;
}
</style>
